// https://www.npmjs.com/package/react-scroll
import { Link } from "react-scroll";
// https://react-icons.github.io/react-icons/
import { FiMail } from "react-icons/fi";
import { FaChevronCircleUp } from "react-icons/fa";
import { contactInfo } from "../../data";
import profilePic from "../images/final.png";

const mail = `mailto:${contactInfo.email}`;
const phone = `tel:${contactInfo.phone}`;

const Contact = ({ theme, avatar, name }) => {
  const newTheme = `${theme} d-flex flex-column min-vh-100 justify-content-center`;

  return (
    <section id="contact" className={newTheme}>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
      <div className="container text-center">
        <h2>Contact</h2>
        <hr />
        <div className="row align-items-center">
          <div className="col-lg-6">
            <img src={profilePic} alt={name} />
          </div>
          <div className="col-lg-6">
            <h3>{name}</h3>
            {contactInfo.email && contactInfo.email !== "" ? (
              <a href={mail}>
                Email <br></br><span>{contactInfo.email}</span>
              </a>
            ) : (
              ""
            )}
            <br />
            {contactInfo.phone && contactInfo.phone !== "" ? (
              <a href={phone}>
                Phone <br></br><span>{contactInfo.phone}</span>
              </a>
            ) : (
              ""
            )}
            {(!contactInfo.email || contactInfo.email === "") &&
            (!contactInfo.phone || contactInfo.phone === "") ? (
              <a href={contactInfo.mailChimp} target="_blank" rel="noreferrer">
                <button type="button" className="btn btn-secondary btn-lg">
                  <FiMail /> Contact me
                </button>
              </a>
            ) : (
              ""
            )}
            <br />
            <a href={contactInfo.linkedin} target="_blank" rel="noreferrer">LinkedIn</a><br></br>
            <a href={contactInfo.github} target="_blank" rel="noreferrer">Github</a><br></br>
            <a href={contactInfo.stackoverflow} target="_blank" rel="noreferrer">Stack Overflow</a><br></br>
            <a href={contactInfo.hackerrank} target="_blank" rel="noreferrer">Hacker Rank</a>
          </div>
        </div>
        <Link className="scroll" to="home" smooth={true} duration={750}>
          <FaChevronCircleUp id="scroll-up" />
        </Link>
      </div>
      <br></br>
            <br></br>
            <br></br>
            <br></br>
    </section>
  );
};

export default Contact;
